.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul,ol,li{
  list-style: none;
}
a{
  text-decoration: none;
  color: inherit;
}
img{
  vertical-align: top;
  max-width: 100%;
}
#header{
  width: 100%;
  height: 122px;
  position: relative;
  z-index: 10;
  background: transparent;
}
#header .up{
  width: 1100px;
  height: 78px;
  margin: 0 auto;
  position: relative;
}
.left{
  width: 250px;
  float: left;
  font-size: 14px;
  position: relative;
}
.language{
  position: absolute;
  width: 250px;
  height: 35px;
  line-height: 35px;
  left: 0;
  top: 10px;
  z-index: 20;
}
.language>a{
  position: absolute;
  width: 250px;
  height: 22px;
  left: 0;
  top: 6px;
  line-height: 22px;
  color: #fff;
  text-align: left;
  padding-left: 30px;
}
.language>a>span{
  display: inline-block;
  width: 14px; height: 8px;
  background: url(../public/images/h_0.png);
  margin-left: 15px;
}
#header .language>a>span>img{
  display: none;  
}
#header:hover .language a{
  color: #333;
}
#header:hover .language>a>span{
  background: none;
}
#header:hover .language>a>span>img{
  display: block;
}

.list{
  position: absolute;
  width: 200px;
  background: #fff;
  font-size: 12px;
  text-align: left;
  padding: 10px 0;
  left: 0;
  top: 45px;
  line-height: 26px;
  border: 1px solid #000;
  z-index: 21;
}
.list li:hover{
  background: #ccc;
}
.list li a{
  padding: 10px;
}
#logo{
  width: 222px;
  height: 42px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
#logo a{
  display: block;
  height: 42px;
  background: url(../public/images/logo_1.png) no-repeat left top/cover;
}
.util{
  float: right;
}
.util ul{
  display: flex;
  margin-top: 20px;
  font-size: 14px;
}
.util ul li{
  padding: 0 10px;
  border-right: 1px solid #ccc;
}
.util ul li:last-child{
  border-right: none;
}
.util ul li a{
  color: #fff;
}
.util ul li:last-child a{
  display: block;
  text-indent: -9999px;
  width: 33px;
  height: 18px;
  background: url(../public/images/ser_0.png) no-repeat center/auto;
}

#header:hover{
  background: #fff;
}

#header:hover a{
  color: #666;
}
#header:hover #logo a{
  background-image: url(../public/images/logo_0.png);
}
#header:hover .util li:last-child a{
  background-image: url(../public/images/ser_1.png);
}

#nav{
  height: 44px;
  text-align: center;
  position: relative;
}
#nav>ul{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 44px;
}
#nav>ul>li{
  padding: 0 15px;
}
#nav>ul>li>a{
  display: block;
  height: 44px;
  line-height: 44px;
  color: #fff;
  position: relative;
}
#nav>ul>li>a:after{
  content: '';
  display: block;
  position: absolute;
  width: 0%;
  height: 4px;
  background: #333;
  left: 0;
  bottom: 0;
  transition: all .3s;
}
#nav>ul>li>a:hover:after{
  width: 100%;
}
#nav .subback{
  position: absolute;
  width: 100%;
  background: #fff;
  left: 0;
  text-align: right;
  z-index: 10;
  padding: 10px 0;
  border-top: 1px solid #aaa;
  display: none;
}
#nav>ul>li:hover .subback{
  display: block;
}
#nav .subback .subin{
  width: 1100px;
  margin: 0 auto;
}
#nav .subback .subin .sub{
  width: 500px;
  float: right;
  text-align: left;
  margin-right: 90px;
  display: flex;
  gap: 20px;  
  font-size: 14px;
  line-height: 24px;
}
#nav>ul>li:nth-child(6) .subback .subin .sub{
  margin-right: 100px;
}
#nav>ul>li:nth-child(7) .subback .subin .sub{
  margin-right: 5px;
}
#nav>ul>li:nth-child(8) .subback .subin .sub{
  width: 100%;
  text-align: center;
  margin-right: -310px;
}
#nav>ul>li:nth-child(8) .subback .subin .sub>li:nth-child(2) ol{
  font-size: 14px;  
}
#nav>ul>li:nth-child(8) .subback .subin .sub>li:nth-child(2) ol a{
  color: #ccc;
  line-height: 20px;
}
#nav>ul>li:nth-child(8) .subback .subin .sub>li:nth-child(2) ol a:hover{
  color: #000;
}
#nav .subback .subin .sub>li>a{
  color: #ccc;
}
#nav .subback .subin .sub>li>a:hover{
  color: #000;
}
.main{
  width: 100%;
  height: 676px;
  position: relative;
  top: -122px;
  overflow: hidden;
}

.main .imgB img{
  width: 100%;
  height: 676px;  
  object-fit: cover;
}
.main .txtB{
  position: absolute;
  width: 1440px;
  height: 80px;
  left: 50%;
  transform: translateX(-50%);
  bottom: 160px;
  text-align: left;
  opacity: 0;
  animation-name: op;
  animation-duration: 2.5s;
  animation-delay: .5s;
  animation-iteration-count: infinite;
  animation-fill-mode: backwards;
}
@keyframes op{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}
.main .txtB p:nth-child(1){
  font-size: 18px;
  height: 27px;
  color: #222;
}
.main .txtB p:nth-child(2){
  font-size: 36px;
  height: 53px;
}
.main .li:nth-child(2) .txtB p,
.main .li:nth-child(3) .txtB p,
.main .li:nth-child(5) .txtB p{
  color: #fff;
}
.s-next,
.s-prev{
  position: absolute;
  width: 68px;
  height: 68px;
  top: 50%;
  transform: translateY(-50%);
  text-indent: -9999px;
  background: rgba(255,255,255,.5) url(../public/images/arr_l.png);
  z-index: 10;
  cursor: pointer;
}
.s-prev{
  left: 30px;
}
.s-next{
  right: 30px;
  background-image: url(../public/images/arr_r.png);
}
.section{
  width: 1440px;
  height: auto;
  margin: 0 auto;
}
#section1{
  height: 706px;
}
#section1 h2{
  width: 1440px;
  height: 62px;
  font-size: 28px;
  padding-bottom: 20px;
  text-align: left;
}
#section1 .boxWrap{
  height: 670px;
}
#section1 .boxWrap .slide{
