}
#section2{
  height: 540px;
  margin-top: 120px;
  position: relative;
}
#section2 .txt{
  position: absolute;
  width: 226px;
  height: 174px;
  z-index: 10;
  left: 170px;
  bottom: 80px;
  text-align: left;
  color: #fff;
}
#section2 .txt h2{
  font-size: 32px;
  width: 280px;
  height: 48px;
}
#section2 .txt p{
  width: 280px;height: 61px;
  font-size: 18px;
  padding-top: 7px;
  letter-spacing: -1px;
  line-height: 26px;
}
#section2 .txt p>span{
  display: block;
}
#section2 .txt>span{
  display: block;
  width: 114px;height: 39px;
  padding: 8px 23px;
  border: 1px solid #fff;
  font-size: 14px;
  letter-spacing: -1px;
  margin-top: 26px;
}
#section3{
  height: 651px;
  margin-top: 120px;
}
#section3 h2{
  width: 1440px;height: 62px;
  font-size: 28px;
  text-align: left;
  padding-bottom: 20px;
}
#section3 .boxWrap{
  height: 589px;
}
#section3 .boxWrap>ul{
  width: 1440px;
  height: 522px;overflow: hidden;
