}
#section3 .boxWrap>ul>li:nth-child(1) .img,
#section3 .boxWrap>ul>li:nth-child(3) .img{
  width: 342px;
  height: 342px;
  position: absolute;
  left: 0;
  top: 0;
  padding: 12px;
}
#section3 .boxWrap>ul>li:nth-child(2) .img{
  position: absolute;
  width: 708px;height: 522px;
  left: 0; top: 0;
}
#section3 .boxWrap>ul>li:nth-child(1) .txt,
#section3 .boxWrap>ul>li:nth-child(3) .txt{
  position: absolute;
  width: 342px;
  height: 96px;
