}
#section4{
  width: 1440px;height: 718px;
  overflow: hidden;
  margin-top: 120px;
}
#section4 ul{
  width: 1440px;height: 718px;
  display: flex;
  justify-content: space-between;
}
#section4 ul li{
  width: 707px;
  height: 718px;
  color: #222;
}
#section4 ul li .img{
  height: 599px;
}
#section4 ul li .txt{
  height: 119px;
}
#section4 ul li .txt h3{
  width: 707px;height: 78px;
