}

#footer{
  height: 156px;
  margin-top: 80px;
  padding-bottom: 37px;
  font-size: 12px;
  color: #767676;
  border-top: 1px solid #767676;
}
#footer .fup{
  height: 55px;
}
#footer .fup .footIn{
  width: 1200px;
  padding: 0 50px;
  height: 55px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
#footer .fup .fmenu{
  width: 820px;
  height: 55px;
}
#footer .fup .fmenu ul{
  width: 820px;
  height: 55px;
  display: flex;
  align-items: center;
}
#footer .fup .fmenu ul li a{
  display: block;
  padding: 3px 14px 3px 0;
  border-right: 1px solid #767676;
  margin-left: 14px;
}
#footer .fup .fmenu ul li:nth-child(3) a{
  font-weight: 700;
  font-size: 13px;
  color: #000;
}
#footer .fup .fmenu ul li:first-child a{
  margin-left: 0;
}
#footer .fup .fmenu ul li a:hover{
  text-decoration: underline;
}
#footer .fup .fmenu ul li:last-child a{
  border-right: none;
}
#footer .fup .fsns{
  width: 240px;
  height: 55px;
}
#footer .fup .fsns ul{
  display: flex;
  width: 240px;
  height: 55px;
  justify-content: space-between;
  align-items: center;
}
#footer .fup .fsns ul li{
  width: 30px;
  height: 30px;
}
#footer .fup .fsns ul li:first-child{
  width: 42px;
  height: 28px;
}
#footer .fdown{
  height: 63px;
  border-top: 1px solid #767676;
}
#footer .footIn{
  width: 1200px;
  height: 63px;
  text-align: left;
  margin: 0 auto;
  padding: 0 50px;
}
#footer .footIn .tel{
  width: 820px;height: 43px;
  padding-top: 10px;
  font-size: 15px;
}
#footer .footIn .copy{
  font-size: 14px; height: 20px;
}












@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
